body {
  margin: 0px;
  padding: 0px;
  font-family: "PingFang TC", "Helvetica Neue", Helvetica, Arial, "微軟正黑體", "Microsoft JhengHei", sans-serif;
  font-weight: 400;
  color: #444;
  font-size: 16px;
  overflow-x: hidden;
  background-color: #f9f9f9;
}

h1 {
  font-size: 32px;
  line-height: 1.5em;
}

h2, h3 {
  font-weight: 400;
  letter-spacing: 2px;
  line-height: 1.5em;
}
h2 {
  font-size: 26px;
  color: #FCD406;
}
p {
  font-size: 18px;
}
p, span, button, a {
  font-weight: 300;
  letter-spacing: 1.2px;
  line-height: 1.5em;
}
a {
  color: white;
}
a:hover {
  color: #FCD406;
}
hr {
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(linear, right top, left top, from(#444), color-stop(#FCD406), to(#444));
  background-image: -webkit-linear-gradient(right, #444, #FCD406, #444);
  background-image: -o-linear-gradient(right, #444, #FCD406, #444);
  background-image: linear-gradient(to left, #444, #FCD406, #444);
}


